<template>
    <div class="container my-5 d-flex flex-column align-items-center">
        <h2 class="text-center title">Tous les tests</h2>

        <div v-if="status_get_tests == 'loading'" class="spinner-border my-2" role="status">
            <span class="sr-only">Loading...</span>
        </div>

        <div v-else-if="status_get_tests == 'error'" class="text-danger">
            <p class="m-0 font-italic">Une erreur s'est produite</p>
        </div>

        <div v-else class="card-deck flex-column mt-4">
            <div v-for="test in tests" :key="test.id" class="card test-card text-white mb-4 d-flex flex-column align-items-center">
                <div class="card-body d-flex flex-column text-center">
                    <h5 class="card-title">{{test.name}}</h5>
                    <p class="card-text">{{test.short_description}}</p>
                    <div class="mt-auto">
                        <router-link :to="'/test/'+test.name_id" class="btn btn-primary">
                            <i class="fa-solid fa-rocket"></i>
                            Faire le test
                        </router-link>
                    </div>
                </div>
            </div>


            <!-- Pagination -->
            <!-- <nav aria-label="Page navigation">
                <ul class="pagination justify-content-center">
                    <li class="page-item"><a class="page-link" href="#">1</a></li>
                    <li class="page-item disabled"><span class="page-link">...</span></li>
                    <li class="page-item"><a class="page-link" href="#">3</a></li>
                    <li class="page-item active"><span class="page-link">4</span></li>
                    <li class="page-item"><a class="page-link" href="#">5</a></li>
                    <li class="page-item disabled"><span class="page-link">...</span></li>
                    <li class="page-item"><a class="page-link" href="#">10</a></li>
                </ul>
            </nav> -->
        </div>
    </div>
</template>

<script>
import axios from 'axios'
// import Title from '@/components/elements/Title.vue'
// import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        // Title,
        // Btn
    },
    data () {
        return {
            status_get_tests: "loading",
            tests: []
        }
    },
    methods: {
        get_tests() {
            let data = {
                "token": localStorage.getItem("way_token")
            }
            axios
                .get(this.$store.state.url_b+"/api/front/test/tests-get-all", {
                    params: data
                })
                .then(res => {
                    this.tests = res.data
                    this.status_get_tests = "home"
                })
                .catch(error => {
                    console.log(error)
                    this.status_get_tests = "error"
                })
                .finally(() => {

                })
        }
    },
    created() {
        this.get_tests()
    }
}
</script>

<style scoped>
.title {
    font-size: 2.5rem;
    font-weight: 600;
    color: #ffffff;
}

.test-card {
    background-color: #2c2c54;
    border: none;
}

.card-title {
    font-weight: 600;
    font-size: 1.25rem;
}

.card-text {
    font-size: 1rem;
    color: #a9a9b3;
}

.btn-primary {
    background-color: #f14668;
    border-color: #f14668;
}

.pagination .page-link {
    color: #f14668;
}

.pagination .active .page-link {
    background-color: #f14668;
    border-color: #f14668;
    color: #ffffff;
}

.pagination .disabled .page-link {
    color: #a9a9b3;
}

.pagination .page-link:hover {
    background-color: #f14668;
    color: #ffffff;
}

.card-deck {
    display: flex;
    flex-direction: column;
}
</style>