<template>
    <LoadingPage v-if="status == 'loading'" :text="'Résultat...'" />
    <ErrorLoadingPage v-else-if="status == 'error'" :text="error_loading_page_text" />
    <div v-else class="container my-5">
        <div class="result-title">{{res.test_data.result_intro}}</div>
        <img 
            :src="res.image_url" 
            class="result-photo img-fluid rounded mx-auto d-block" 
            width="300px"
        >
        <div class="result-name">{{res.result.name}}</div>
        <div class="result-description mb-5">
            {{res.result.description}}
        </div>


        <div class="row">
            <div class="col">
                <ul class="nav nav-tabs">
                    <li class="nav-item rounded">
                        <a class="nav-link active" href="#result_tab" data-toggle="tab">Analyse</a>
                    </li>
                    <li class="nav-item rounded">
                        <a class="nav-link" href="#answers_tab" data-toggle="tab">Réponses au test</a>
                    </li>
                </ul>

                <div class="tab-content bg-light text-dark pb-3">
                    <div class="tab-pane active in" id="result_tab">
                        <!-- Personality Info Cards -->
                        <div class="result-part p-1">
                            <div v-for="(attribute, i) in res.result.attributes" :key="i" class="personality-info-card m-3 p-3 rounded text-left">
                                <strong>{{i+1}}. {{attribute.attr}} : </strong>
                                <span>{{attribute.expl}}</span>
                            </div>
                        </div>

                        <!-- Conclusion Card -->
                        <div class="result-part">
                            <div class="conclusion-card mx-3 mt-2 p-1 rounded">
                                <strong>Conclusion</strong>
                                <p class="m-0 p-1">{{res.result.conclusion}}</p>
                            </div>
                        </div>

                        <!-- Quote Section -->
                        <div class="result-part d-flex justify-content-center mt-4">
                            <div class="quote p-0 col-md-6 p-2">
                                <p class="quote-text m-0 p-0 text-center">« {{res.result.quote.quote}} »</p>
                                <span class="quote-author text-center">- {{res.result.quote.author}}</span>
                            </div>
                        </div>
                    </div>

                    <div class="tab-pane p-2" id="answers_tab">
                        <div class="">
                            <!-- Responses Content -->
                            <h4 class="question_name m-2">{{res.test_data.name}}</h4>
                            <div v-for="(q, i) in res.user_answers" :key="i" class="question mb-3 p-2 rounded text-left">
                                <p class="m-0 p-1 question_text ">{{i+1}}. {{q.q}}</p>
                                <div class="answers">
                                    <div v-for="(a, j) in q.ans" :key="j" class="answer m-2 p-1 rounded" :class="{'chosen': q.aid == a.id}">
                                        {{a.text}}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>




        <div class="mt-3 d-flex justify-content-center">
            <div class="col-md-4 list-buttons">
                <button v-if="!link_copied" class="btn btn-light btn-block button-copy" @click="copy_link()">
                    <i class="fa-solid fa-copy"></i>
                    Copier le lien du résultat
                </button>

                <button v-else class="btn btn-light btn-block button-copied" disabled>
                    <i class="fa-solid fa-check"></i>
                    Copié
                </button>

                <router-link class="btn btn-light btn-block button-test" :to="'/test/'+res.test_data.name_id">
                    <i class="fa-solid fa-rotate-left"></i>
                    Repasser ce test
                </router-link>

                <router-link class="btn btn-light btn-block button-list" to="/tests">
                    <i class="fa-solid fa-list"></i>
                    Liste des tests
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
// import Title from '@/components/elements/Title.vue'
// import Btn from '@/components/elements/Btn.vue'
import LoadingPage from '@/components/home/LoadingPage.vue'
import ErrorLoadingPage from '@/components/home/ErrorLoadingPage.vue'

export default {
    components: {
        // Title,
        // Btn,
        LoadingPage,
        ErrorLoadingPage
    },
    data () {
        return {
            status: "loading",
            error_loading_page_text: '',
            res: {
                image_url: "",
                result: {
                    attributes: [],
                    conclusion: "",
                    description: "",
                    name: "",
                    quote: {
                        author: "",
                        quote: ""
                    }
                },
                test_data: {
                    name: "",
                    name_id: "",
                    result_intro: ""
                },
                user_answers: []
            },
            timer_copy_link: null,
            link_copied: false
        }
    },
    computed: {

    },
    methods: {
        get_result_data() {
            let code = this.$route.params.code
            let data = {
                "token": localStorage.getItem("way_token")
            }
            axios
                .get(this.$store.state.url_b+"/api/front/result/get/"+code, {
                    params: data
                })
                .then(res => {
                    this.res = res.data
                    this.status = "result"
                })
                .catch(error => {
                    console.log(error)
                    this.error_loading_page_text = "Une erreur est survenue"
                    if (error.response && error.response.data) {
                        switch (error.response.data) {
                            case "result_not_found" :
                                this.error_loading_page_text = "Le résultat n'existe pas";
                                break;
                            default :
                                break;
                        }
                    }
                    this.status = "error"
                })
                .finally(() => {

                })
        },
        make_link_copied_false_in_moment() {
            this.timer_copy_link = setTimeout(function(){
                this.link_copied = false
            }.bind(this), 2000)
        },
        copy_link() {
            this.link_copied = true
            this.make_link_copied_false_in_moment()
            if (!navigator.clipboard) {
                const textArea = document.createElement("textarea");
                textArea.value = window.location.href
                    
                // Maka textarea opacity 0 so it's not visible
                textArea.style.position = "absolute";
                textArea.style.opacity = 0;
                    
                document.body.prepend(textArea);
                textArea.select();

                try {
                    document.execCommand('copy');
                } catch (error) {
                    console.error(error);
                } finally {
                    textArea.remove();
                }
            }
            else {
                navigator.clipboard.writeText(window.location.href)
            }
        },
    },
    created() {
        this.get_result_data()
    }
}
</script>

<style scoped>
.container {
/*    background-color: #1a1a2e;*/
    color: #ffffff;
    font-family: 'Helvetica', sans-serif;
    text-align: center;
/*    padding: 20px;*/
}
