    font-size: 32px;
    /*background-image: linear-gradient(90deg, rgba(201,205,23,1) 0%, rgba(255,0,125,1) 100%);
    color: transparent;
    background-clip: text;*/
    color: rgba(201,205,23,1);
    font-weight: bold;
    margin-bottom: 10px;
}
