<template>
    <nav id="navbar1" class="navbar navbar-expand-lg navbar-dark fixed-top p-0 px-1">
        <router-link to="/" class="navbar-brand">
            <img class="logo-title" :src="require('@/assets/img/title_logo.png')">
        </router-link>
        <button ref="btnToggler" class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
            aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"
            @click="click_navbar_toggler">
            <span class="navbar-toggler-icon"></span>
        </button>
        <div ref="collapse" class="collapse navbar-collapse" id="navbarNav">
            <ul class="navbar-nav ml-auto">
                <li class="nav-item p-1">
                    <router-link class="nav-link" to="/tests">
                        <i class="fa-solid fa-list"></i>
                        Liste des tests
                    </router-link>
                </li>
                <li class="nav-item p-1">
                    <router-link class="nav-link" to="/about">
                        <i class="fa-solid fa-circle-info"></i>
                        À propos
                    </router-link>
                </li>
            </ul>
        </div>
    </nav>
    <FadeScreen v-if="show_fade_screen" />
</template>

<script>
import FadeScreen from '@/components/home/FadeScreen.vue'

export default {
    components: {
        FadeScreen
    },
    data () {
        return {
            show_fade_screen: false
        }
    },
    methods: {
        close_navbar() {
            if (this.$refs.collapse.classList.contains("show")) {
                this.$refs.btnToggler.click()
                this.show_fade_screen = false
            }
        },
        click_navbar_toggler() {
            if (!this.$refs.collapse.classList.contains("show")) {
                this.show_fade_screen = true
            }
            else {
                this.show_fade_screen = false
            }
        }
    },
    mounted () {
        document.addEventListener('click', this.close_navbar)
    },
    beforeUnmount () {
        document.removeEventListener('click',this.close_navbar)
    }
}
</script>

<style scoped>
.navbar-brand {
    height: 50px;
}

.logo-title {
    height: 100%;
    object-fit: cover;
}

.navbar {
    background-color: #1a1a2e;

}

.navbar-brand,
.navbar-nav .nav-link {
    color: #f4f4f4 !important;
}

.navbar-collapse {

}

.navbar-toggler {
    border-color: #f4f4f4;
}

.navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(244, 244, 244, 0.5)' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}
</style>