<template>
    <div class="container text-center my-5 d-flex flex-column align-items-center">
        <h1 class="title">What are you ?</h1>
        <p class="lead subtitle">Découvre qui (ou ce que) tu es vraiment avec nos tests !</p>

        <h2 class="text-white mt-5">Tests populaires</h2>

        <div v-if="status_get_tests == 'loading'" class="spinner-border my-2" role="status">
            <span class="sr-only">Loading...</span>
        </div>

        <div v-else-if="status_get_tests == 'error'" class="text-danger">
            <p class="m-0 font-italic">Une erreur s'est produite</p>
        </div>

        <div v-else class="card-deck mt-4">
            <div v-for="test in tests" :key="test.id" class="card test-card text-white">
                <div class="card-body d-flex flex-column">
                    <h5 class="card-title">{{test.name}}</h5>
                    <p class="card-text">{{test.short_description}}</p>
                    <div class="mt-auto">
                        <router-link :to="'/test/'+test.name_id" class="btn btn-primary">
                            <i class="fa-solid fa-rocket"></i>
                            Faire le test
                        </router-link>
                    </div>
                </div>
            </div>
        </div>

        <router-link to="/tests" class="btn btn-outline-light btn-lg mt-5">
            <i class="fa-solid fa-list"></i>
            Voir tous les tests
        </router-link>
    </div>
</template>

<script>
import axios from 'axios'
// import Title from '@/components/elements/Title.vue'
// import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        // Title,
        // Btn
    },
    data () {
        return {
            status_get_tests: "loading",
            tests: []
        }
    },
    methods: {
        get_tests() {
            let data = {
                "token": localStorage.getItem("way_token")
            }
            axios
                .get(this.$store.state.url_b+"/api/front/test/home-get-all", {
                    params: data
                })
                .then(res => {
                    this.tests = res.data
                    this.status_get_tests = "home"
                })
                .catch(error => {
                    console.log(error)
                    this.status_get_tests = "error"
                })
                .finally(() => {

                })
        }
    },
    created() {
        this.get_tests()
    }
}
</script>

<style scoped>
.title {
    font-size: 3.5rem;
    font-weight: 600;
    color: #ffffff;
    text-transform: uppercase;
}

.subtitle {
    font-size: 1.25rem;
    color: #f14668; /* Rose flashy */
}

.test-card {
    background-color: #2c2c54; /* Bleu foncé pour les cartes */
    border: none;
}

.card-title {
    font-weight: 600;
    font-size: 1.25rem;
}

.card-text {
    font-size: 1rem;
    color: #a9a9b3;
}

.btn-primary {
    background-color: #f14668;
    border-color: #f14668;
}

.btn-outline-light {
    border-color: #f14668;
    color: #f14668;
}

.btn-outline-light:hover {
    background-color: #f14668;
    color: #ffffff;
}

.card-body {
    display: flex;
    flex-direction: column;
}
</style>