<template>
    <div v-if="status == 'loading'" id="loading-screen" class="">
        <div class="loading-text">Passage au test...</div>
        <div class="loading-animation">
            <div class="loading-box"></div>
            <div class="loading-box"></div>
            <div class="loading-box"></div>
            <div class="loading-box"></div>
        </div>
    </div>
    <div v-else-if="status == 'error'" id="error-section">
        <div class="error-message">{{error_loading_page_text}}</div>
        <div class="error-illustration">
            <!-- Simple illustration -->
            <div class="error-icon">&#9888;</div>
        </div>
        <button class="btn btn-primary" onclick="location.reload()">Réessayer</button>
    </div>
    <div v-else class="container my-5">
        <h3 class="text-center mb-4 test-name">{{DATA_FETCHED.test.name}}</h3>
        <p class="text-center mb-4">{{DATA_FETCHED.test.long_description}}</p>

        <!-- Consignes -->
        <div class="row mb-4 d-flex justify-content-center">
            <div v-for="(instruction, iindex) in DATA_FETCHED.test.instructions" :key="iindex" class="col-md-4 d-flex align-items-stretch">
                <div class="consigne-card flex-fill">
                    <p class="text-center">{{instruction}}</p>
                </div>
            </div>
        </div>

        <!-- Questions -->
        <div class="row d-flex flex-column align-items-center">
            <div v-for="(question, qindex) in data_in_play.questions" :key="qindex" :id="'q'+qindex" class="col-md-8">
                <div class="card">
                    <div class="card-body">
                        <h5 class="card-title">
                            {{qindex+1}}. {{DATA_FETCHED.questions.text[question.id]}}
                        </h5>
                        <div class="">
                            <div v-for="(answer_id, aindex) in DATA_FETCHED.answers.order[question.id]" :key="aindex" class="answer my-2 p-2"
                                :class="{'chosen': data_in_play.questions[qindex].answer_chosen_index == aindex, 'unclickable': wait_results}"
                                @click="click_on_answer(qindex, aindex)"
                            >
                                <span>
                                    {{DATA_FETCHED.answers.text[answer_id]}}
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
        <!-- Le bouton apparaîtra après la dernière question -->
        <div class="text-center mt-4">
            <div v-if="error_result_text" class="m-0">
                <p class="m-0 mb-3 text-danger font-italic">{{error_result_text}}</p>
            </div>
            <button v-if="test_finished" class="btn btn-primary" id="result-button" :disabled="wait_results" @click="get_test_results()">
                Voir le résultat
                <div v-if="wait_results" class="spinner-border spinner-border-sm" role="status">
                    <span class="sr-only">Loading...</span>
                </div>
            </button>

            <p v-else class="text-center m-0 p-0 nb-q-left">
                Il reste {{nb_q_left}} {{nb_q_left > 1 ? 'questions' : 'question'}} à répondre
            </p>
            
        </div>
    </div>
</template>

<script>
import axios from 'axios'
// import Title from '@/components/elements/Title.vue'
// import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        // Title,
        // Btn
    },
    data () {
        return {
            "DATA_FETCHED": {
                "test": {
                    "result_id": 0,
                    "name_id": "",
                    "name": "",
                    "long_description": "",
                    "instructions": []
                },
                "questions": {
                    "text": {},
                    "order": []
                },
                "answers": {
                    "text": {},
                    "order": {}
                },
                
            },
            "data_in_play": {
                "questions": [
                    {
                        "id": "1",
                        "answer_chosen_index": -1
                    }
                ]
            },
            "test_finished": false,
            "wait_results": false,
            "status": "loading",
            "error_loading_page_text": "",
            "error_result_text": ""
        }
    },
    computed: {
        nb_q_left() {
            let nb = this.DATA_FETCHED.questions.order.length - this.data_in_play.questions.length + 1
            return nb
        }
    },
    methods: {
        get_test_data() {
            let test_name_id = this.$route.params.name_id
            let data = {
                "token": localStorage.getItem("way_token"),
                "name_id": test_name_id
            }
            axios
                .get(this.$store.state.url_b+"/api/front/test/get", {
                    params: data
                })
                .then(res => {
                    this.DATA_FETCHED = res.data
                    this.data_in_play.questions[0]["id"] = this.DATA_FETCHED.questions.order[0]
                    this.warn_changing_page()
                    this.status = "game"
                })
                .catch(error => {
                    console.log(error)
                    this.error_loading_page_text = "Une erreur est survenue"
                    if (error.response && error.response.data) {
                        switch (error.response.data) {
                            case "not_found" :
                                this.error_loading_page_text = "Le test n'existe pas";
                                break;
                            case "not_public" :
                                this.error_loading_page_text = "Le test n'est pas en mode publique";
                                break;
                            default :
                                break;
                        }
                    }
                    this.status = "error"
                })
                .finally(() => {

                })
            },
        warn_changing_page() {
            this.$store.dispatch('set_is_in_game', true)
            window.addEventListener('beforeunload', this.before_window_unload)
        },
        before_window_unload(e) {
            e.preventDefault()
            let return_value = "Vous êtes sur le point de quitter cette page"
            e.returnValue = return_value
            return return_value
        },
        scroll_to(id) {
            setTimeout(function () {
                const element = document.getElementById(id);
                const navbar = document.getElementById('navbar1')
                
                let navbarHeight = navbar ? navbar.scrollHeight : 0
    
                if (element) {
                    window.scrollTo({
                        behavior: 'smooth', // Ajoute une animation fluide
                        top: element.offsetTop - navbarHeight - 3, // Scroll jusqu'au début de l'élément
                    });
                }
            }, 50);
        },
        click_on_answer(question_index, answer_index) {
            if (this.wait_results) {
                return true
            }

            this.data_in_play.questions[question_index].answer_chosen_index = answer_index
            if (this.data_in_play.questions.length != this.DATA_FETCHED.questions.order.length) {
                if (question_index == this.data_in_play.questions.length - 1) {
                    this.data_in_play.questions.push({
                        "id": this.DATA_FETCHED.questions.order[question_index+1],
                        "answer_chosen_index": -1
                    })

                    let next_question_id = 'q'+(question_index+1)
                    this.scroll_to(next_question_id)
                }
            }
            else {
                let should_scroll_to_button = false
                if (question_index == this.data_in_play.questions.length - 1) {
                    if (!this.test_finished) {
                        should_scroll_to_button = true
                    }
                    this.test_finished = true
                    if (should_scroll_to_button) {
                        this.scroll_to("result-button")
                    }
                }
            }
        },
        get_test_results() {
            this.wait_results = true
            this.error_result_text = ''
            let data = {
                "answers": this.data_in_play.questions,
                "result_id": this.DATA_FETCHED.test.result_id,
                "token": localStorage.getItem("way_token")
            }

            axios.post(this.$store.state.url_b+'/api/front/test/get-result', data)
                .then(res => {
                    console.log(res.data)
                    if (res.data.code) {
                        this.$store.dispatch('set_is_in_game', false)
                        window.removeEventListener('beforeunload', this.before_window_unload)
                        this.$router.push('/result/'+res.data.code)
                    }
                    else {
                        this.error_result_text = "Une erreur s'est produite. Veuillez réessayer."
                        this.wait_results = false
                    }
                })
                .catch(error => {
                    console.error(error)
                    this.error_result_text = "Une erreur s'est produite. Veuillez réessayer."
                    this.wait_results = false
                });
        }
    },
    created() {
        this.get_test_data()
    },
    beforeUnmount() {
        this.$store.dispatch('set_is_in_game', false)
        window.removeEventListener('beforeunload', this.before_window_unload)
    }
}
</script>

<style scoped>
/* Page de test */
/*.container {
    background-color: #2c2c54;
}*/

.test-name {
    color : #f14668;
}

/* Cartes de consignes */
.consigne-card {
/*    background: linear-gradient(135deg, #ff6f61, #ff9671);*/
/*    background: linear-gradient(135deg, #f5f5f5, #d3d3d3);*/
/*    background: linear-gradient(135deg, #3a3a70, #2c2c54);*/
/*    background: linear-gradient(135deg, #f7f7e8, #e0e0cc);*/

    background: linear-gradient(135deg, #f14668, #2c2c54);
    color: #ffffff;
/*    color: #000000;*/
    padding: 20px;
    margin-bottom: 15px;
    border-radius: 12px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

p {
    margin: 0;
    font-size: 1.1rem;
    font-weight: 500;
}

/* Question */
.card {
    margin-bottom: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.card-title {
    font-size: 1.5rem;
    color: #2c2c54;
}
.card-text {
    font-size: 1rem;
    color: #6c757d;
}

.answer {
    border : 1.5px solid #2c2c54;
    color: #2c2c54;
    border-radius: 6px;
}

.answer.chosen {
    background-color : #2c2c54;
    color: white;
}

.answer.unclickable {
    opacity: 0.5;
}

@media (hover: hover) {
    .answer:hover {
        box-shadow: 0px 0px 12px #2c2c54;
        cursor: pointer;
    }
}

@media (hover: hover) {
    .answer.unclickable:hover {
        box-shadow: none;
        cursor: default;
    }
}

.nb-q-left {
    font-style: italic;
    font-size: 1rem;
    color: lightgrey;
    font-family: 'Poppins', sans-serif;
}

.btn-primary {
    background-color: #f14668;
    border-color: #f14668;
}

/* Loading the test */
#loading-screen {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

/* Loading text */
.loading-text {
    font-size: 2rem;
    color: #f14668; /* Rose flashy */
    margin-bottom: 20px;
}

/* Loading animation */
.loading-animation {
    display: flex;
    justify-content: space-around;
    width: 120px;
}

.loading-box {
    width: 20px;
    height: 20px;
    background: linear-gradient(135deg, #f14668, #2c2c54);
    animation: bounce 1.5s infinite ease-in-out;
    border-radius: 4px;
}

@keyframes bounce {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-15px);
    }
}

.loading-box:nth-child(2) {
    animation-delay: 0.3s;
}

.loading-box:nth-child(3) {
    animation-delay: 0.6s;
}

.loading-box:nth-child(4) {
    animation-delay: 0.9s;
}

/* Error page */
#error-section {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.error-message {
    font-size: 1.5rem;
    color: #ff4f4f; /* Rouge pour l'erreur */
    margin-bottom: 20px;
}

.error-illustration {
    margin-bottom: 20px;
}

.error-icon {
    font-size: 5rem;
    color: #ff4f4f; /* Rouge pour l'erreur */
}

.btn-primary {
    background-color: #f14668;
    border-color: #f14668;
}
</style>