import Test from '@/views/test/Test.vue'
import Result from '@/views/test/Result.vue'
import ListTest from '@/views/test/ListTest.vue'

var TestRoute = [
  {
    path: '/test/:name_id',
    name: 'Test',
    component: Test
  },
  {
    path: '/tests',
    name: 'Tests',
    component: ListTest
  },
  {
    path: '/result/:code',
    name: 'Result',
    component: Result
  },
]

export default TestRoute
